import Styled from "styled-components";
import { Link } from "react-router-dom";

const IndexPage = Styled.div`
  width: 100%;
  min-height: 100%;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  position: relative;

  .EX_P {
    width: 100%;
    padding: 0.2667rem 0 0.32rem;
    display: flex;
    justify-content: center;
  }
`;

IndexPage.Banner = Styled.img`
  width: 100%;
  display: block;
`;

IndexPage.QueryContent = Styled.div`
  margin-top: 0.5334rem;
  color: #626262;
  font-size: 0.3734rem;
  display: flex;
  justify-content: space-between;

  p {
    margin-bottom: 0.16rem;
    display: flex;
    align-items: center;
  }

  img {
    width: 0.5334rem;
    height: 0.5334rem;
    margin-right: 0.16rem;
    display: block;
  }
`;

IndexPage.QueryContent.Container = Styled.div`
  padding: 0.5334rem 1.0667rem 0;
  flex-grow: 1;
`;

IndexPage.QueryContent.Title = Styled.div`
  color: #4EA0FF;
  font-size: 0.4rem;
  display: flex;
  justify-content: center;
  position: relative;

  &:before {
    content: "";
    width: 100%;
    height: 0.0267rem;
    top: 50%;
    left: 0;
    margin-top: -0.0134rem;
    background-color: #B9D2EF;
    position: absolute;
  }

  p {
    padding: 0 0.08rem;
    background-color: #FFFFFF;
    position: relative;
  }
`;

IndexPage.Example = Styled(Link)`
  color: #4EA0FF;
  font-size: 0.4rem;
  border-bottom: 1px solid;
`;

IndexPage.Button = Styled(Link)`
  width: 100%;
  color: #FFFFFF;
  font-size: 0.3734rem;
  line-height: 1.3334rem;
  text-align: center;
  background-color: #4EA0FF;
`;

IndexPage.My = Styled(Link)`
  width: 1.3334rem;
  right: 0;
  bottom: 1.4934rem;
  display: block;
  position: absolute;

  img {
    width: 100%;
    display: block;
  }
`;

export default IndexPage;
